import { useState, useRef } from 'react';
import { motion } from 'framer-motion';

const Downloads = () => {
  const [downloading, setDownloading] = useState(null);
  const canvasRef = useRef(null);

  const assets = [
    {
      id: 'hd-1080p',
      title: 'HD Wallpaper 1080p',
      description: 'Full HD desktop/TV wallpaper',
      filename: 'christmas-profile-hd-1080p',
      path: '/assets/christmas-profile-hd-1080p.svg',
      dimensions: '1920 x 1080',
      category: 'wallpaper'
    },
    {
      id: 'facebook-cover',
      title: 'Facebook Cover',
      description: 'Facebook page cover photo',
      filename: 'facebook-cover-1200x630',
      path: '/assets/social/facebook-cover-1200x630.svg',
      dimensions: '1200 x 630',
      category: 'social'
    },
    {
      id: 'instagram-post',
      title: 'Instagram Post',
      description: 'Square post for Instagram feed',
      filename: 'instagram-post-1080x1080',
      path: '/assets/social/instagram-post-1080x1080.svg',
      dimensions: '1080 x 1080',
      category: 'social'
    },
    {
      id: 'instagram-story',
      title: 'Instagram Story',
      description: 'Vertical story format',
      filename: 'instagram-story-1080x1920',
      path: '/assets/social/instagram-story-1080x1920.svg',
      dimensions: '1080 x 1920',
      category: 'social'
    },
    {
      id: 'twitter-header',
      title: 'Twitter/X Header',
      description: 'Twitter profile header banner',
      filename: 'twitter-header-1500x500',
      path: '/assets/social/twitter-header-1500x500.svg',
      dimensions: '1500 x 500',
      category: 'social'
    },
    {
      id: 'youtube-thumbnail',
      title: 'YouTube Thumbnail',
      description: 'Video thumbnail 16:9',
      filename: 'youtube-thumbnail-1280x720',
      path: '/assets/social/youtube-thumbnail-1280x720.svg',
      dimensions: '1280 x 720',
      category: 'social'
    },
    {
      id: 'linkedin-cover',
      title: 'LinkedIn Cover',
      description: 'LinkedIn company page banner',
      filename: 'linkedin-cover-1584x396',
      path: '/assets/social/linkedin-cover-1584x396.svg',
      dimensions: '1584 x 396',
      category: 'social'
    },
    {
      id: 'profile-picture',
      title: 'Profile Picture',
      description: 'Square profile avatar',
      filename: 'profile-picture-400x400',
      path: '/assets/social/profile-picture-400x400.svg',
      dimensions: '400 x 400',
      category: 'social'
    }
  ];

  const convertSvgToImage = async (svgPath, format, filename) => {
    return new Promise(async (resolve, reject) => {
      try {
        const response = await fetch(svgPath);
        const svgText = await response.text();
        
        const parser = new DOMParser();
        const svgDoc = parser.parseFromString(svgText, 'image/svg+xml');
        const svgElement = svgDoc.documentElement;
        
        const width = parseInt(svgElement.getAttribute('width')) || 1920;
        const height = parseInt(svgElement.getAttribute('height')) || 1080;
        
        const canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext('2d');
        
        const img = new Image();
        const svgBlob = new Blob([svgText], { type: 'image/svg+xml;charset=utf-8' });
        const url = URL.createObjectURL(svgBlob);
        
        img.onload = () => {
          ctx.drawImage(img, 0, 0, width, height);
          URL.revokeObjectURL(url);
          
          let mimeType = 'image/png';
          let quality = 1.0;
          
          if (format === 'jpg') {
            mimeType = 'image/jpeg';
            quality = 0.95;
          }
          
          canvas.toBlob((blob) => {
            const downloadUrl = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = downloadUrl;
            link.download = `${filename}.${format}`;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            URL.revokeObjectURL(downloadUrl);
            resolve();
          }, mimeType, quality);
        };
        
        img.onerror = reject;
        img.src = url;
      } catch (error) {
        reject(error);
      }
    });
  };

  const handleDownload = async (asset, format) => {
    const downloadId = `${asset.id}-${format}`;
    setDownloading(downloadId);
    
    try {
      if (format === 'svg') {
        const response = await fetch(asset.path);
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `${asset.filename}.svg`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
      } else {
        await convertSvgToImage(asset.path, format, asset.filename);
      }
    } catch (error) {
      console.error('Download failed:', error);
      alert('Download failed. Please try again.');
    }
    
    setTimeout(() => setDownloading(null), 500);
  };

  const DownloadButton = ({ asset, format, label }) => {
    const downloadId = `${asset.id}-${format}`;
    const isDownloading = downloading === downloadId;
    
    return (
      <button
        onClick={() => handleDownload(asset, format)}
        disabled={isDownloading}
        className="flex items-center justify-center gap-1 px-3 py-2 rounded-lg font-medium text-sm transition-all duration-200"
        style={{
          background: isDownloading ? 'rgba(0,255,255,0.3)' : 'rgba(0,255,255,0.15)',
          color: '#00FFFF',
          border: '1px solid rgba(0,255,255,0.3)',
          minWidth: '70px'
        }}
      >
        {isDownloading ? (
          <svg className="w-4 h-4 animate-spin" viewBox="0 0 24 24" fill="none">
            <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="3" strokeLinecap="round" strokeDasharray="32" strokeDashoffset="32" />
          </svg>
        ) : (
          <>
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
            </svg>
            {label}
          </>
        )}
      </button>
    );
  };

  const wallpapers = assets.filter(a => a.category === 'wallpaper');
  const socialAssets = assets.filter(a => a.category === 'social');

  return (
    <div className="min-h-screen py-20 px-4" style={{ background: '#1e2f3c' }}>
      <canvas ref={canvasRef} style={{ display: 'none' }} />
      
      <div className="max-w-6xl mx-auto">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-12"
        >
          <h1 className="text-4xl md:text-5xl font-bold text-white mb-4">
            Download Assets
          </h1>
          <p className="text-lg text-gray-400">
            Christmas 2025 branded assets for eSIM Myanmar - All formats available
          </p>
        </motion.div>

        {/* HD Wallpapers */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
          className="mb-12"
        >
          <h2 className="text-2xl font-bold text-white mb-6 flex items-center gap-3">
            <span className="w-8 h-8 rounded-lg flex items-center justify-center" style={{ background: 'rgba(0,255,255,0.2)' }}>
              <svg className="w-5 h-5 text-cyan-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
            </span>
            HD Wallpapers
          </h2>
          
          {wallpapers.map((asset) => (
            <div
              key={asset.id}
              className="rounded-2xl p-6 mb-4"
              style={{
                background: 'rgba(255,255,255,0.05)',
                backdropFilter: 'blur(12px)',
                border: '1px solid rgba(255,255,255,0.1)'
              }}
            >
              <div className="flex flex-col lg:flex-row gap-6">
                <div 
                  className="w-full lg:w-80 h-44 rounded-xl overflow-hidden flex-shrink-0"
                  style={{ background: '#0f1a24' }}
                >
                  <img src={asset.path} alt={asset.title} className="w-full h-full object-cover" />
                </div>
                <div className="flex-grow">
                  <h3 className="text-xl font-semibold text-white mb-2">{asset.title}</h3>
                  <p className="text-gray-400 mb-3">{asset.description}</p>
                  <p className="text-sm text-gray-500 mb-4">{asset.dimensions}</p>
                  <div className="flex flex-wrap gap-2">
                    <DownloadButton asset={asset} format="svg" label="SVG" />
                    <DownloadButton asset={asset} format="png" label="PNG" />
                    <DownloadButton asset={asset} format="jpg" label="JPG" />
                  </div>
                </div>
              </div>
            </div>
          ))}
        </motion.div>

        {/* Social Media Assets */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="text-2xl font-bold text-white mb-6 flex items-center gap-3">
            <span className="w-8 h-8 rounded-lg flex items-center justify-center" style={{ background: 'rgba(0,255,255,0.2)' }}>
              <svg className="w-5 h-5 text-cyan-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8h2a2 2 0 012 2v6a2 2 0 01-2 2h-2v4l-4-4H9a1.994 1.994 0 01-1.414-.586m0 0L11 14h4a2 2 0 002-2V6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2v4l.586-.586z" />
              </svg>
            </span>
            Social Media Assets
          </h2>
          
          <div className="grid md:grid-cols-2 gap-4">
            {socialAssets.map((asset) => (
              <div
                key={asset.id}
                className="rounded-xl p-4"
                style={{
                  background: 'rgba(255,255,255,0.05)',
                  backdropFilter: 'blur(12px)',
                  border: '1px solid rgba(255,255,255,0.1)'
                }}
              >
                <div 
                  className="w-full h-32 rounded-lg overflow-hidden mb-4"
                  style={{ background: '#0f1a24' }}
                >
                  <img src={asset.path} alt={asset.title} className="w-full h-full object-contain" />
                </div>
                <h3 className="text-lg font-semibold text-white mb-1">{asset.title}</h3>
                <p className="text-sm text-gray-400 mb-1">{asset.description}</p>
                <p className="text-xs text-gray-500 mb-3">{asset.dimensions}</p>
                <div className="flex flex-wrap gap-2">
                  <DownloadButton asset={asset} format="svg" label="SVG" />
                  <DownloadButton asset={asset} format="png" label="PNG" />
                  <DownloadButton asset={asset} format="jpg" label="JPG" />
                </div>
              </div>
            ))}
          </div>
        </motion.div>

        {/* Color Palette */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
          className="mt-12 rounded-2xl p-6"
          style={{
            background: 'rgba(255,255,255,0.05)',
            border: '1px solid rgba(255,255,255,0.1)'
          }}
        >
          <h3 className="text-xl font-semibold text-white mb-4">Brand Color Palette</h3>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center">
              <div className="w-full h-16 rounded-lg mb-2" style={{ background: '#1e2f3c' }} />
              <p className="text-sm text-gray-400">Dark Blue</p>
              <p className="text-xs text-gray-500 font-mono">#1e2f3c</p>
            </div>
            <div className="text-center">
              <div className="w-full h-16 rounded-lg mb-2" style={{ background: '#00FFFF' }} />
              <p className="text-sm text-gray-400">Cyan</p>
              <p className="text-xs text-gray-500 font-mono">#00FFFF</p>
            </div>
            <div className="text-center">
              <div className="w-full h-16 rounded-lg mb-2" style={{ background: '#F8F9FA' }} />
              <p className="text-sm text-gray-400">Pearl</p>
              <p className="text-xs text-gray-500 font-mono">#F8F9FA</p>
            </div>
            <div className="text-center">
              <div className="w-full h-16 rounded-lg mb-2 border border-gray-600" style={{ background: 'rgba(255,255,255,0.1)' }} />
              <p className="text-sm text-gray-400">Glass</p>
              <p className="text-xs text-gray-500 font-mono">Transparent</p>
            </div>
          </div>
        </motion.div>

        {/* PSD Note */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
          className="mt-6 p-4 rounded-xl text-center"
          style={{ background: 'rgba(0,255,255,0.1)', border: '1px solid rgba(0,255,255,0.2)' }}
        >
          <p className="text-sm text-gray-300">
            <strong className="text-cyan-400">PSD Files:</strong> Download SVG and open in Adobe Photoshop to edit as vector smart objects. Export as PSD from Photoshop.
          </p>
        </motion.div>
      </div>
    </div>
  );
};

export default Downloads;
